import { LoadingIndicator } from '../../../pages/LoadingIndicator';
import { useAppSelector } from '../../../utils/hooks';
import styles from './IngredientDetails.module.css';
import { useParams } from 'react-router-dom';

function IngredientDetails(): React.ReactElement {
  const ingredients = useAppSelector((store) => store.ingredients);
  const { id } = useParams();
  const ingredient = ingredients.find((item) => item._id === id);

  //    ,   
  if (!ingredient) {
    return (
      <LoadingIndicator />
    );
  }
  //       
  return (
    <div className={styles.ingredientInfo}>
      <p className="text text_type_main-medium"> </p>
      <img src={ingredient.image_large} alt={ingredient.name} />
      <p className="text text_type_main-default pt-4 pb-8">{ingredient.name}</p>
      <div className={styles.description}>
        <div>
          <p className="text text_type_main-default text_color_inactive">, </p>
          <p className="text text_type_digits-default text_color_inactive">{ingredient.calories}</p>
        </div>
        <div>
          <p className="text text_type_main-default text_color_inactive">, </p>
          <p className="text text_type_digits-default text_color_inactive">{ingredient.proteins}</p>
        </div>
        <div>
          <p className="text text_type_main-default text_color_inactive">, </p>
          <p className="text text_type_digits-default text_color_inactive">{ingredient.fat}</p>
        </div>
        <div>
          <p className="text text_type_main-default text_color_inactive">, </p>
          <p className="text text_type_digits-default text_color_inactive">{ingredient.carbohydrates}</p>
        </div>
      </div>
    </div>
  );
};

export { IngredientDetails };
