import styles from './App.module.css';
import { AppHeader } from '../AppHeader/AppHeader';
import { Main } from '../Main/Main';
import { Route, Routes, useLocation, useNavigate } from 'react-router-dom';
import { Login } from '../../pages/Login';
import { Registration } from '../../pages/Registration';
import { ForgotPassword } from '../../pages/ForgotPassword';
import { ResetPassword } from '../../pages/ResetPassword';
import { Profile } from '../../pages/Profile';
import { ProtectedRoute } from '../ProtectedRoute/ProtectedRoute';
import { useEffect } from 'react';
import { IngredientDetails } from '../BurgerIngredients/IngredientDetails/IngredientDetails';
import { getIngredients } from '../../services/actions/ingredientsActions';
import { useAppDispatch } from '../../utils/hooks';
import { Feed } from '../../pages/Feed/Feed';
import { ProfileOrders } from '../../pages/ProfileOrders';
import FeedOrderDetails from '../FeedOrders/FeedOrderDetails/FeedOrderDetails';

function App(): React.ReactElement {
  const dispatch = useAppDispatch();
  const location = useLocation();
  const navigate = useNavigate();

  //   "location.state.ingredientPopupOpened"
  //    ,        <Main />    <IngredientDetails />
  //     , "ingredientPopupOpened"   "true"
  //
  //   "false"    ,      "/ingredients/:id",   <IngredientDetails />
  //   "true"      "/ingredients/:id",      <Main />
  const isPopupOpened = location.state?.ingredientPopupOpened

  //         ,      
  useEffect(() => {
    dispatch(getIngredients());
    //  "isPopupOpened" "true"     (    "/ingredients/:id"   F5,    "/ingredients/:id")
    if (isPopupOpened) {
      //    "location.state.ingredientPopupOpened"  "false"   
      //    ,     "isPopupOpened"  "false"    <IngredientDetails />
      navigate(
        location.pathname,
        {
          state: { ingredientPopupOpened: false },
          replace: true
        }
      );
    }
  }, []);

  return (
    <div className={styles.app}>
      <>
        <AppHeader />
        <Routes>
          <Route
            index
            element={
              <Main />
            }
          />
          <Route
            path="/profile"
            element={
              <ProtectedRoute>
                <Profile />
              </ProtectedRoute>
            }
          />
          <Route
            path="/profile/orders"
            element={
              <ProtectedRoute>
                <ProfileOrders />
              </ProtectedRoute>
            }
          />
          <Route
            path="/profile/orders/:id"
            element={
              <ProtectedRoute>
                {
                  isPopupOpened ?
                    <ProfileOrders /> :
                    <div className="mt-30">
                      <FeedOrderDetails />
                    </div>
                }
              </ProtectedRoute>
            }
          />
          <Route
            path="/login"
            element={
              <ProtectedRoute redirectToHomeIfLoggedIn>
                <Login />
              </ProtectedRoute>
            }
          />
          <Route
            path="/register"
            element={
              <ProtectedRoute redirectToHomeIfLoggedIn>
                <Registration />
              </ProtectedRoute>
            }
          />
          <Route
            path="/forgot-password"
            element={
              <ProtectedRoute redirectToHomeIfLoggedIn>
                <ForgotPassword />
              </ProtectedRoute>
            }
          />
          <Route
            path="/reset-password"
            element={
              <ProtectedRoute redirectToHomeIfLoggedIn>
                <ResetPassword />
              </ProtectedRoute>
            }
          />
          <Route
            path="/ingredients/:id"
            element={
              isPopupOpened ?
                <Main /> :
                <div className="mt-30">
                  <IngredientDetails />
                </div>
            }
          />
          <Route
            path="/feed"
            element={
              <Feed />
            }
          />
          <Route
            path="/feed/:id"
            element={
              isPopupOpened ?
                <Feed /> :
                <div className="mt-30">
                  <FeedOrderDetails />
                </div>
            }
          />
        </Routes>
      </>
    </div>
  );
}

export { App };